import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import { GlobalValidate } from '../../../../_services/validate.service';

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./ticket-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class SupportTicketAddComponent    {
    @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	  
	ticket = new ticketData();
	ticketForm: FormGroup;  
	 
	ticketId = 0;
	status:string;
	msg = []; 
	bootstrapFormValues = [];
	 
	public queue = [];
	public type = [];
	public Module = [];
	
	constructor(private http: HttpClient, private router:Router, private route:ActivatedRoute, private globalValid: GlobalValidate) { 
		this.route.queryParams.filter(params => params.ticketId).subscribe(params=>{
			if(params.ticketId != undefined){
				this.ticketId = params.ticketId; 
				}
		}); 
	
		if(this.ticketId == 0){
			this.status="Add";
//			this.populateDropDownData();
		}else{
			this.status="Update";
			this.populateData();
		}
	}
	 
	ngAfterViewInit(){
	  }
 
 	 toggleAnimation(){
			this.router.navigate(['portal/support-ticket-list']);
		 }
	
	ngOnInit() {
		
		this.ticketForm = new FormGroup({		
//			'branchName' : new FormControl('', [ Validators.required, Validators.minLength(3)] ),
//			'branchInvoiceName' : new FormControl('', [ Validators.required] ),
		});
		
	}
	  
//	  get branchName(){ return this.branchForm.get('branchName')};
//	  get branchInvoiceName(){ return this.branchForm.get('branchInvoiceName')};
//	  get branchCode(){ return this.branchForm.get('branchCode')};

	populateDropDownData(){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/queue',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.queue = data['populateQueue'];
			  															});
		  
		  	this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/type',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.type = data['populateType'];
			  															});
		
			 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/module',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.Module = data['populateModule'];
			  															});	
		
	  }
	  
	 
	  populateData(){
		const headers = new HttpHeaders({'Content-Type':'application/json'});
		
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/ticket-data',
									JSON.stringify({ ticketId :this.ticketId,
												   	requestType : "update" }),
		{ headers, withCredentials: true }).subscribe(
							data => {
										this.ticket = data['populateData'][0];
									},
							(err) => {
										console.log("ERROR: "+err);
									 },
							 () => {
										this.populateDropDownData();
							}
					);
		}

	 	onSubmit() {
	
		this.bootstrapFormValues = $('#m_form_1').serializeArray();
			
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				 	 
				this.ticket[this.bootstrapFormValues[i].name] = this.bootstrapFormValues[i].value;
			}

	console.log(this.ticket);	
			
		 if(!this.ticketForm.invalid){ /* && this.errorMsg == ''*/
	console.log("valid");
		
		const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.ticketId == 0){
			this.ticket['requestType'] = 'add';
		}else{
			this.ticket['requestType'] = 'update';
			this.ticket['ticketId'] = this.ticketId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/ticket',
									JSON.stringify(this.ticket),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
			 											this.ticketForm.reset();
														this.toggleAnimation();
														}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  }
	  }
	  
	  
	  onDelete(){
		 this.ticket['requestType'] = 'delete';
		 this.ticket['empId'] = this.ticketId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/ticket',
									JSON.stringify({ requestType : 'delete',
												   'ticketId' : this.ticketId }),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
			 											this.ticketForm.reset();
														this.toggleAnimation();
														}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		 
	 }

}



export class ticketData
{

}


